.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.css_addr {
  font-size: 18px;
  color: #2980b9;
}

h1 {
  text-align: center;
  margin-bottom: 30px;
  font-size: 30px;
  font-weight: 700;
  color: #3498db;
}

button {
  padding: 10px 20px;
  margin-top: 20px;
  font-size: 18px;
  background-color: #3498db;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

button:hover {
  background-color: #2980b9;
}

.text-center {
  text-align: center !important;
}

.fadeInUp {
  animation-name: fadeInUp;
}

.information {
  border: 1px solid;
  text-align: left;
  line-height: 30px;
  margin-top: 2%;
  margin-left: 20%;
  margin-right: 20%;
  padding: 20px;;
}

label, span {
  color: #3498db;
}

.input_text {
  margin: 17px;
  padding: 8px;
  border-radius: 5px;
  border: 1px solid #3498db;
  width: 400px;
  height: 20px;
  color:#2980b9;
}

.font_red {
  color: red;
}
